.skill {
  width: 350px;
  margin: 0 10px 20px;
  padding: 30px 15px 40px 15px;
  background: #f8f9fa;
  text-align: center;
  border: 1px dotted #e7e7e7;
  border-radius: 4px;

  .iconBox {
    margin-bottom: 15px;

    .icon {
      display: inline-block;
      width: 76px;
      height: 76px;
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }

    .html5 {
      background-image: url("../../../assets/icons/icon-html5.svg");
    }

    .css3 {
      background-image: url("../../../assets/icons/icon-css3.svg");
    }

    .js {
      background-image: url("../../../assets/icons/icon-js.svg");
    }

    .react {
      background-image: url("../../../assets/icons/icon-react.svg");
    }

    .redux {
      background-image: url("../../../assets/icons/icon-redux.svg");
    }
  }

  .skillContent {
    h3 {
      margin-top: 22px;
      font-size: 22px;
      font-weight: 600;
    }

    .description {
      font-size: 15px;
      line-height: 1.8em;
      color: #797979;
    }
  }
}
